<template >
  <v-container>
        <NavBarp :p_name="professor.name"></NavBarp>

    <v-alert text v-model="alert.show" :type="alert.type" dismissible>
      {{ alert.message }}
    </v-alert>

    <h1 class="font-weight-light">Mis Cursos </h1>
    <v-row justify="center">
      <v-card
        class="ma-3"
        max-width="344"
        v-for="courses in coursesList"
        :key="courses.id"
        elevation="24"
      >
        <v-img
          src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
          height="200px"
        ></v-img>
        <v-card-title>{{ courses.c_name }}</v-card-title>
        <v-card-subtitle>{{ courses.c_description }}</v-card-subtitle>
        <v-card-actions>
          <v-btn color="blue" small dark fab @click="readCourse(courses.id_c)"
            ><v-icon>mdi-pencil</v-icon></v-btn
          >
          <v-btn color="red" small dark fab @click="readCourseDelete(courses)"
            ><v-icon>mdi-delete</v-icon></v-btn
          >
          <v-spacer></v-spacer>

          <v-btn color="purple" text @click="$router.push(`/professor/assignments/${courses.id_c}`)" >
              Asignaciones
          </v-btn>


        </v-card-actions>
      </v-card>
    </v-row>

    <v-dialog v-model="add" max-width="450px">
      <v-card>
        <v-card-title>Crear curso</v-card-title>
        <v-card-text>
          <v-form ref="addForm" @submit.prevent="addCourse()">
            <v-text-field
              prepend-icon="mdi-biohazard"
              label="Name"
              :rules="[(v) => !!v || 'Nombre es requerido']"
              v-model="courseToAdd.c_name"
            ></v-text-field>
            <v-textarea
              prepend-icon="mdi-bike"
              label="Description"
              :rules="[(v) => !!v || 'Descripcón necesaria']"
              v-model="courseToAdd.c_description"
            ></v-textarea>
            <v-btn block class="success mt-3" type="submit">add</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog v-model="edit" max-width="450px">
      <v-card>
        <v-card-title>modificar Curso</v-card-title>
        <v-card-text>
          <v-form ref="editForm" @submit.prevent="editCourse()">
            <v-text-field
              prepend-icon="mdi-biohazard"
              label="Name"
              :rules="[(v) => !!v || 'Nombre es requerido']"
              v-model="courseToEdit.c_name"
            ></v-text-field>

            <v-textarea
              prepend-icon="mdi-bike"
              label="Description"
              :rules="[(v) => !!v || 'Descripcón necesaria']"
              v-model="courseToEdit.c_description"
            ></v-textarea>
            <v-btn block class="info mt-3" type="submit">Editar</v-btn>

          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>

  


 <v-dialog v-model="eliminar" max-width="450px">
      <v-card>
        <v-card-title>¿Desea Eliminar {{courseToDelete.c_name}}?</v-card-title>
        <v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer> 
                <v-btn color="red darken-1"
                v-model="courseToDelete.id_c"
                text @click="deleteCourse(courseToDelete.id_c)">
                  Eliminar
                </v-btn>
                <v-btn color="blue darken-1"
                text @click="eliminar=false">
                  Cancelar
                </v-btn>
            </v-card-actions>

        </v-card-text>
      </v-card>
    </v-dialog>

  
    <v-btn @click="add = true" color="green" large right fixed bottom fab dark>
      <v-icon>mdi-plus</v-icon>
    </v-btn>
 <v-btn
      @click="$router.push(`/profile`)"
      color="primary"
      large
      left
      fixed
      bottom
      fab
      dark
    >
      <v-icon>mdi-undo-variant</v-icon>
    </v-btn>

  </v-container>
</template>

<script>
import NavBarp from "@/components/Navbarp.vue";

export default {
  data: () => ({
    professor: {},
    coursesList: [],
    courseToAdd: {},
    courseToEdit: {},
    courseToDelete:{},
    add: false,
    edit: false,
    eliminar:false,
    alert: {
      show: false,
      message: "Error",
      type: "error",
    },
  }),

  methods: {
    async addCourse() {
      let valid = this.$refs.addForm.validate();

      if (valid) {
        this.courseToAdd.id = this.professor.id;

        try {
          const res = await this.axios.post(
            "/professor/course",
            this.courseToAdd
          );

          this.coursesList.push(res.data.course);
          this.coursesList.reverse();

          this.$refs.addForm.reset();
          this.add = false;
          this.alert.show = true;
          this.alert.type = "success";
          this.alert.message = res.data.message;
        } catch (error) {
          console.log(error);
        }
      }
    },

    async readCourseDelete(course) {

      this.courseToDelete=course;
      this.eliminar=true;
     console.log(this.courseToDelete); 

    },

    async deleteCourse(id_c) {

    try {
        const res = await this.axios.delete(`/professor/course/${id_c}`);

        let pos = this.coursesList.findIndex((c) => c.id_c == id_c);
        this.coursesList.splice(pos, 1);
        this.alert.type="error";
        this.alert.show=true;
        this.alert.message=res.data.message;
        this.eliminar=false;

      } catch (error) {
        console.log(error);
      }


    },
    async editCourse() {
      //console.log(this.courseToEdit.id_c);
      let valid = this.$refs.editForm.validate();

      if (valid) {
        try {
          const res = await this.axios.put(
            `/professor/course/${this.courseToEdit.id_c}`,
            this.courseToEdit
          );

          //obtenego el indice en donde se encuentra en el arreglo el registro a modificar.
          const index = this.coursesList.findIndex(
            (c) => c.id_c == this.courseToEdit.id_c
          );

          this.coursesList[index] = this.courseToEdit;
          this.edit = false;
          this.alert.show = true;
          this.alert.message =
            "El curso " + this.courseToEdit.c_name + ", editado exitosamente.";
          this.alert.type = "success";

        } catch (error) {
          console.log(error);
          this.alert.show = true;
          this.alert.message = error.response.data.message;
          this.alert.type = "error";
        }
      }

      //actualizar arregflo de la lista
    },
    async readCourse(id_c) {
      try {
        const res = await this.axios.get(`/professor/course/${id_c}`);
        this.courseToEdit = res.data.course;
        this.edit = true;
        //console.log(this.coursesList);
        //console.log(id_c);
      } catch (error) {
        console.log(error);
      }
    },
  },
  components:{
    NavBarp
  },
  created: async function () {
    this.professor = JSON.parse(sessionStorage.getItem("session"));
    if (this.professor == null) {
      this.$route.push("/");
    } else if (this.professor.role != "professor") {
      this.$route.push("/profile");
      //redirecciona a las vistas
    } else {
      try {
        const res = await this.axios.post(
          "/professor/my-courses",
          this.professor
        );
     //   console.log(res.data);
        this.coursesList = res.data;
      } catch (error) {
        console.error(error);
      }
    }
  },

};
</script>