<template >
  <v-container class="text-center">

    <NavBarp :p_name="professor.name"></NavBarp>

    <h1 class="font-weight-light">{{ course.c_name }}</h1>
    <h2 class="font-weight-thin">{{ course.c_description }}</h2>

    <v-alert
      class="text-start"
      text
      v-model="alert.show"
      :type="alert.type"
      dismissible
    >
      {{ alert.message }}
    </v-alert>

    <v-row justify="center">
      <v-col md="9" sm="12">
        <v-simple-table fixed-header>
          <thead>
            <tr>
              <th class="text-center">Name</th>
              <th class="text-center">Descripcón</th>
              <th class="text-center">Archivo</th>
              <th class="text-center">Entregas</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="asign in assignmentsList" :key="asign.id_a">
              <td>{{ asign.a_name }}</td>
              <td>{{ asign.a_description }}</td>

              <td>
                <v-icon color="red" dark @click="openURL(asign.a_file)"
                  >mdi-file-pdf</v-icon
                >
              </td>
              <td>
                <v-icon color="cyan darken-1" @click="getDeliveries(asign.id_a)"
                  >mdi-book-open-page-variant</v-icon
                >
              </td>
            </tr>
          </tbody>
        </v-simple-table>
      </v-col>
    </v-row>

    <v-dialog v-model="showDelv" max-width="450px">
      <v-simple-table fixed-header>
        <thead>
          <tr>
            <th class="text-center">Alumno</th>
            <th class="text-center">Entrega</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="delivery in deliveriesList" :key="delivery.id_d" class="text-center">
            <td>{{delivery.s_name}}</td>
            <td>
              <a @click="openURL(delivery.d_file)">{{delivery.d_filename}}</a>
            </td>

          </tr>
        </tbody>
      </v-simple-table>
    </v-dialog>





    <v-dialog v-model="add" max-width="450px">
      <v-card>
        <v-card-title> Crear Asignación </v-card-title>
        <v-card-text>
          <v-form
            ref="NewAssignmentForm"
            class="ma-3"
            @submit.prevent="addAssignment()"
          >
            <v-file-input
              label="file"
              :rules="[(v) => !!v || 'Campo obligatorio']"
              v-model="assignmentToAdd.a_file"
            ></v-file-input>
            <v-text-field
              prepend-icon="mdi-biohazard"
              label="Name"
              v-model="assignmentToAdd.a_name"
              :rules="[(v) => !!v || 'Campo obligatorio']"
            ></v-text-field>
            <v-textarea
              prepend-icon="mdi-bike"
              label="Description"
              :rules="[(v) => !!v || 'Descripcón necesaria']"
              v-model="assignmentToAdd.a_description"
            ></v-textarea>
            <v-btn
              block
              class="success mt-3"
              type="submit"
              :loading="cargar"
              :disabled="cargar"
              >Añadir</v-btn
            >
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-btn @click="add = true" color="green" large right fixed bottom fab dark>
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <v-btn
      @click="$router.push(`/professor/courses`)"
      color="primary"
      large
      left
      fixed
      bottom
      fab
      dark
    >
      <v-icon>mdi-undo-variant</v-icon>
    </v-btn>
  </v-container>
</template>

<script>

import NavBarp from '@/components/Navbarp.vue';


export default {
  data: () => ({
    professor: {},
    course: {},
    assignmentToAdd: {},
    add: false,
    cargar: false,
    assignmentsList: [],
    deliveriesList: [],
    show_progress: true,
    showDelv:false,
    alert: {
      show: false,
      message: "Error",
      type: "error",
    },
  }),

  created: function () {
    this.show_progress = true;

    this.professor = JSON.parse(sessionStorage.getItem("session"));

    if (this.professor == null) {
      this.$router.push("/");
    } else if (this.professor.role != "professor") {
      this.$router.push("/profile");
    } else {
      //promesas infomacion del curso
      this.axios
        .get(`/professor/course/${this.$route.params.id_c}`)
        .then((res) => {
          this.course = res.data.course;
          //console.log(this.course);
        })
        .catch((e) => {
          console.log(e);
        });

      //asignments
      this.axios
        .get(`/professor/course-assignments/${this.$route.params.id_c}`)
        .then((res) => {
          this.assignmentsList = res.data.assignment;
          //console.log(this.assignmentsList);
        })
        .catch((e) => {
          console.log(e);
        });

      this.show_progress = false;
    } //end if
  },
  methods: {
    async addAssignment() {
      this.cargar = true;
      let valid = this.$refs.NewAssignmentForm.validate();
      if (valid) {
        try {
          const newAssignment = new FormData();

          newAssignment.append("a_name", this.assignmentToAdd.a_name);
          newAssignment.append(
            "a_description",
            this.assignmentToAdd.a_description
          );

          newAssignment.append("a_file", this.assignmentToAdd.a_file);

          const res = await this.axios.post(
            `/professor/assignment/${this.$route.params.id_c}`,
            newAssignment
          );

          this.assignmentsList.push(res.data.assignment);
          this.add = false;
          this.$refs.NewAssignmentForm.reset();
          this.alert.show = true;
          this.alert.message = "Asignación creada";
          this.alert.type = "success";

          //console.log(assignmentsList);
        } catch (error) {
          console.log(error);
        }

        this.cargar = false;
        //console.log(this.assignmentToAdd);
      }
    },
    async openURL(link) {
      window.open(link);
    },

    async getDeliveries(id_a) {
      try {
        const res = await this.axios.get(`/professor/deliveries/${id_a}`);
        console.log(res.data);
        this.deliveriesList = res.data.deliveries;
        this.showDelv=true;

      } catch (error) {
        console.log(error);
      }
    },
  },
  components:{
    NavBarp
  }
};
</script>