
<template>
  <v-container>
    <NavBarp v-if="user.role=='professor'" :p_name="user.name"></NavBarp>
    <NavbarS v-if="user.role=='student'" :s_name="user.name"></NavbarS>

    <v-row justify="center">
      <v-card>
        <v-card-title>
          {{ user.name }}
        </v-card-title>
        <v-card-subtitle>
          {{ user.role }}
        </v-card-subtitle>
                <v-card-subtitle>
              {{ user.email }}
        </v-card-subtitle>
    
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import NavBarp from "@/components/Navbarp.vue";
import NavbarS from "@/components/NavbarS.vue";
export default {
  data: () => ({
    user: {
      name: "Error",
      role: "error",
      email: "error",
    },
  }),

  created: function () {
    //cuando se carga la vista se cargan los datos del usuario que ingresó
    this.user = JSON.parse(sessionStorage.getItem("session"));
    if (this.user == null) {
      this.$router.push("/");
      //se redirecciona al Home si el objeto storage noe xistwe
    }
  },
  components:{
    NavBarp,
    NavbarS
  }
};
</script>