<template>
  <v-container>
        <NavbarS :s_name="student.name"></NavbarS>

    <v-alert text v-model="alert.show" :type="alert.type" dismissible>
      {{ alert.message }}
    </v-alert>
    <h1 class="font-weight-light">Cursos Disponibles para inscripción</h1>
    <v-row justify="center">
      <v-card
        class="ma-3"
        max-width="344"
        v-for="courses in coursesList"
        :key="courses.id_c"
        elevation="24"
        
      >
        <v-img
          src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
          height="200px"
          class="white--text align-end"
        >
          <v-card-title>{{ courses.c_name }}</v-card-title>
          <v-card-subtitle class="white--text"
            ><b>Profesor:</b>{{ courses.p_name }}</v-card-subtitle
          >
        </v-img>
        <v-card-text>
          <div>
            Lorem ipsum dolor sit amet consectetur.
            <div><b>E-mail:</b>{{ courses.p_email }}</div>
          </div>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="purple"
            :loading="showLoading"
            dark
            absolute
            right
            @click="joinCourse(courses.id_c)"
            >join</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import NavbarS from '@/components/NavbarS'; 

export default {
  data: () => ({
    student: {},
    showLoading: false,
    coursesList: [],
    alert: {
      show: false,
      message: "Error",
      type: "error",
    },
  }),
  created: async function () {
    this.student = JSON.parse(sessionStorage.getItem("session"));
    
    if (this.student == null) {
      this.$router.push("/");
    } else if (this.student.role != "student") {
      this.$router.push("/profile");
    } else {
      try {
        const res = await this.axios.get(`/student/courses/${this.student.id}`);
        this.coursesList = res.data.courses;
       // console.log(this.coursesList);
      } catch (error) {
        console.log(error);
      }
    }
  },

  methods: {
   async joinCourse(id_c) {

      try {
        this.showLoading = true;
        const res = await this.axios.post(`/student/course/${id_c}`, this.student);
        this.alert.show = true;
        this.alert.type = "success";
        this.alert.message = res.data.message;
        this.showLoading = false;

        //se elimina del arreglo el curso al cual se unio el alumno
        let pos = this.coursesList.findIndex((c) => c.id_c == id_c);
        this.coursesList.splice(pos,1);

      } catch (error) {
        this.showLoading = false;
        console.log(error);
        this.alert.show = true;
        this.alert.type = "error";
        this.alert.message = res.data.error;
      }

    },
  },
  components:{
    NavbarS
  }
};
</script>

