<template>
  <v-container class="text-center">
    <NavbarS :s_name="student.name"></NavbarS>

    <h1 class="font-weight-light">{{ course.c_name }}</h1>
    <h2 class="font-weight-thin">
      {{ course.c_description }}
    </h2>

    <v-alert text v-model="alert.show" :type="alert.type" dismissible>
      {{ alert.message }}
    </v-alert>

    <v-row justify="center">
      <v-col md="9" sm="12">
        <v-simple-table fixed-header>
          <thead>
            <tr>
              <th class="text-center">Name</th>
              <th class="text-center">Description</th>
              <th class="text-center">File</th>
              <th class="text-center">UP Delivery</th>
              <th class="text-center">Delivery</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="asign in assignmentList" :key="asign.id_a">
              <th class="text-center">{{ asign.a_name }}</th>
              <th class="text-center">{{ asign.a_description }}</th>

              <th class="text-center">
                <v-icon color="red" @click="openURL(asign.a_file)"
                  >mdi-file-pdf</v-icon
                >
              </th>

              <th v-if="asign.d_file == null" class="text-center">
                <v-icon @click="openForm(asign.id_a)" color="primary"
                  >mdi-cloud-upload</v-icon
                >
              </th>
              <th v-else class="text-center">
                <v-icon color="success">mdi-cloud-check</v-icon>
              </th>

              <th class="text-center">
                <a @click="openURL(asign.d_file)">{{ asign.d_filename }}</a>
              </th>
            </tr>
          </tbody>
        </v-simple-table>
      </v-col>
    </v-row>

    <v-dialog v-model="add" max-width="450">
      <v-card>
        <v-card-title> Subir Entrega </v-card-title>
        <v-card-text>
          <v-form ref="addForm" class="ma-3" @submit.prevent="addDelivery">
            <v-file-input
              label="Entrega"
              v-model="deliveryToAdd.d_file"
              :rules="[(v) => !!v || 'File is required']"
            ></v-file-input>
            <v-btn
              block
              class="success mt-3"
              type="submit"
              :loading="cargar"
              :disabled="cargar"
              >Añadir</v-btn
            >
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-btn
      @click="$router.push(`/student/my-courses/`)"
      color="primary"
      large
      left
      fixed
      bottom
      fab
      dark
    >
      <v-icon>mdi-undo-variant</v-icon>
    </v-btn>
  </v-container>
</template>

<script>
import NavbarS from "@/components/NavbarS";

export default {
  data: () => ({
    student: {},
    assignmentList: [],
    course: {},
    add: false,
    deliveryToAdd: {},
    cargar: false,
    alert: {
      show: false,
      message: "Error",
      type: "error",
    },
  }),
  created: async function () {
    this.student = JSON.parse(sessionStorage.getItem("session"));
    if (this.student == null) {
      this.$router.push("/");
    } else if (this.student.role != "student") {
      this.$router.push("/profile");
    } else {
      try {
        const res = await this.axios.get(
          `/student/assignments/${this.$route.params.id_c}/${this.student.id}`
        );
        this.course = res.data.course;
        this.assignmentList = res.data.assignments;
        //console.log(res);
        //console.log(res);
        // this.coursesList = res.data.courses;
        //    console.log(this.coursesList);
      } catch (error) {
        console.log(error);
      }
    }
  },
  methods: {
    async openURL(link) {
      window.open(link);
    },
    openForm(id_a) {
      this.deliveryToAdd.id_a = id_a;
      this.add = true;
    },
    async addDelivery() {
      let valid = this.$refs.addForm.validate();

      if (valid) {
        this.cargar = true;

        try {
          const delivery = new FormData();

          delivery.append("id", this.student.id);
          delivery.append("id_a", this.deliveryToAdd.id_a);
          delivery.append("d_file", this.deliveryToAdd.d_file);

          const res = await this.axios.post(`/student/delivery`, delivery);
          // console.log(this.deliveryToAdd.d_file);
          this.cargar = false;
          this.add = false;

          let pos = this.assignmentList.findIndex(
            (a) => a.id_a == this.deliveryToAdd.id_a
          );
          this.assignmentList[pos].d_file = this.deliveryToAdd.d_file;
          this.assignmentList[pos].d_filename = this.deliveryToAdd.d_file.name;
          //this.coursesList.splice(pos,1);

          this.alert.message = res.data.message;
          this.alert.type = "success";
          this.alert.show = true;
        } catch (error) {
          console.log(error);
          this.cargar = false;
          this.add = false;
        }
      }
    },
  },
  components: {
    NavbarS,
  },
};
</script>
