<template>
  <v-container>


    <NavbarS :s_name="student.name"></NavbarS>

    <h1 class="font-weight-light">Mis Cursos</h1>
    <v-row justify="center">
      <v-card
        class="ma-3"
        max-width="344"
        v-for="courses in coursesList"
        :key="courses.id_c"
        elevation="24"
      >
        <v-img
          src="https://cdn.vuetifyjs.com/images/parallax/material2.jpg"
          height="200px"
          class="white--text align-end"
          width="344"
        >
          <v-card-title>{{ courses.c_name }}</v-card-title>
          <v-card-subtitle class="white--text">
            <b>Profesor:</b> {{ courses.p_name }}
            <div>
              <b>E-mail:</b>
              {{ courses.p_email }}
            </div>
          </v-card-subtitle>
        </v-img>

        <v-card-text>
          {{ courses.c_description }}
        </v-card-text>

        <v-card-actions>
                    <v-spacer />

          <v-btn color="purple" dark text right @click="$router.push(`/student/assignments/${courses.id_c}`)"> Assignments </v-btn>
        </v-card-actions>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>

import NavbarS from '@/components/NavbarS';

export default {
  data: () => ({
    coursesList: [],
    student: {},
  }),
  created: async function () {
      
    this.student = JSON.parse(sessionStorage.getItem("session"));

    if (this.student == null) {
      this.$router.push("/");
    } else if (this.student.role != "student") {
      this.$router.push("/profile");
    } else {

      try {
        const res = await this.axios.post(`/student/my-courses/`, this.student);
        this.coursesList = res.data.courses;
    //    console.log(this.coursesList);
      } catch (error) {
        console.log(error);
      }

    }
  },
  methods:{

  },
  components:{
    NavbarS
  }
};
</script>

