<template>

    <v-container>
      <v-app-bar app color="primary" dark>
        <v-toolbar-title>
        </v-toolbar-title>
      </v-app-bar>
      <v-alert text v-model="alert.show" :type="alert.type" dismissible>
        {{alert.message}}
      </v-alert>

      <v-row justify="center">
        <v-col class="text-center" md="2" sm="2" >
          <v-btn @click="suForm=true" class="primary">Sign Up</v-btn>
        </v-col>
            <v-col class="text-center" md="2" sm="2" >
          <v-btn @click="suForm=false" class="success">Sign IN</v-btn>
        </v-col>
      </v-row>

    <v-row justify="center">
      <v-col md="6" sm="6">
        <v-card v-if="suForm">
            <v-card-title >Sign up</v-card-title>

              <v-card-text>
                <v-form class="ma-3" @submit.prevent="signUp()" ref="signupForm">
                  <v-text-field 
                  label="Nombre" 
                  prepend-icon="mdi-account"
                  :rules="nameRules"
                  v-model="user.name"
                  ></v-text-field>

                  <v-text-field 
                  label="E-mail"
                  prepend-icon="mdi-email"
                  :rules="emailRules"
                  v-model="user.email"

                  ></v-text-field>

                  <v-text-field label="Contraseña"
                  prepend-icon="mdi-lock"
                  type="password"
                  :rules="passwordRules"
                  v-model="user.password"
                  ></v-text-field>
                  
            <v-radio-group row 
            v-model="user.role"
            :rules="[(v)=>!!v||'Seleccione Opción']" >
              <v-radio label="Professor" value="professor"></v-radio>
              <v-radio label="Student" value="student"></v-radio>
            </v-radio-group>


            <v-btn block class="primary mt-3" type="submit" >Sign Up</v-btn>
                </v-form>

          </v-card-text>

        </v-card>
        
        <v-card v-else>
            <v-card-title >Sign In</v-card-title>

              <v-card-text>
                <v-form class="ma-3" @submit.prevent="signIn()" ref="signInForm">

                  <v-text-field 
                  label="E-mail"
                  prepend-icon="mdi-email"
                  :rules="emailRules"
                  v-model="user.email"

                  ></v-text-field>

                  <v-text-field label="Contraseña"
                  prepend-icon="mdi-lock"
                  type="password"
                  :rules="passwordRules"
                  v-model="user.password"
                  ></v-text-field>
                  
            <v-radio-group row 
            v-model="user.role"
            :rules="[(v)=>!!v||'Seleccione Opción']" >
              <v-radio label="Professor" value="professor"></v-radio>
              <v-radio label="Student" value="student"></v-radio>
            </v-radio-group>


            <v-btn block class="success mt-3" type="submit" >Sign In</v-btn>
                </v-form>

          </v-card-text>

        </v-card>

      </v-col>
    </v-row>


    </v-container>

</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'

export default {
  data: () =>({
    alert:{
      show:false,
      message:'Error',
      type:'error'
    },
    nameRules:[
      value=>!!value||'Campo Obligatorio',
      value => (value && value.length>=5)||'El nombre debe contener más de 5 carácteres'
    ],
    emailRules:[
      value => !!value || 'Campo obligatorio',
      value => /.+@.+\..+/.test(value)||'Ingrese E-mail válido'///expresion para validar un email
    ],
    passwordRules:[
      value => !!value || 'Campo obligatorio',
      value => (value && value.length>=6)||'El password debe contener más de 6 carácteres'

    ],
    user:{name:'',email:'',password:'',role:''},
    suForm:true
  }),


  methods:{
    async signUp(){
      //$refs . Nombre del Formulario.
        let valid=this.$refs.signupForm.validate();
        //console.log(valid);

        if(valid){
          try {
            //se le pasa el body this . user del formulario
              const res = await this.axios.post('/signup',this.user);

              this.$refs.signupForm.reset(); //todos lso cmapso en blanco ene l formulario
              this.suForm=false;
              //  console. log(res);

              this.alert={
                show:true,
                type:'success',
                message:res.data.message
              }


          } catch (error) {
            this.alert = {
              show:true,
              type:'error',
              message:error.response.data.message
            }
          }
        }



     
      },

    async signIn(){
        let valid=this.$refs.signInForm.validate();
        //console.log(valid);

        if (valid) {

          try {
            //se le pasa el objeto ocn los parametros.
               const res = await this.axios.post('/signIn',this.user);
               //este log se ve ene el navegador, ya que es frontend.
               console.log(res);

              if (res.data.Notfound) {
                this.alert = {
                  show:true,
                  message: res.data.message,
                  type:'error'
                }
              }else{
                //si lopgra registrarse se añade a la ssesion storage el objeto usuario.
               // console.log(res.data.name);
                sessionStorage.setItem('session',JSON.stringify(res.data));
                this.$router.push('/profile');
                //se redirecciona a /profile
              }
            
          } catch (error) {
             this.alert = {
              show:true,
              type:'error',
              message:error.response.data.message
            }
          }
        }

    }

  }//methods

}
</script>
